import vantLoading from '../../wxcomponents/vant/loading/index'
export default {
	data() {
		return {
		}
	},
	// 等待父级传入数据
	props: {
		
		// 卡片表头
		title: {
			type: String,
			default () {
				// 表头为空，则直接不渲染这个组件
				return ''
			}
		},
		
		// 右上角 是编辑按钮 还是 切换设备按钮（步数）
		// 除了步数有切换设备按钮外，其他的都是编辑按钮
		// 今日总步数 是否显示 也用这个判断
		editBTN: {
			type: Number,
			default () {
				// 1 代表编辑按钮 2代表切换按钮
				return 2
			}
		},
		
		// 步数来源设备类型
		eqType: {
			type: String,
			default () {
				// 表头为空，则直接不渲染这个组件
				return ''
			}
		},
		
		// 具体数值，最大的数值，有的带有/所以用字符串
		dataNum: {
			type: String,
			default () {
				return '-1'
			}
		},
		
		// 数值的具体单位
		dataCompany: {
			type: String,
			default () {
				return ''
			}
		},
		
		// 标题下方的文字（部分有，部分没有）
		dataTips: {
			type: String,
			default () {
				return ''
			}
		},
		
		// 游标文字的后戳
		cursorSuffix: {
			type: Array,
			default () {
				return []
			}
		},
		
		// 最新数据测量时间
		dataTime: {
			type: String,
			default () {
				return ''
			}
		},
		
		// 数据范围，有可能是多个（目前只有血压是2个范围值，但不一定只有血压，所以做成数组）
		// 数据结构
		// [
		// 	[
		// 		low: num, // 低于范围
		// 		nor: num, // 正常范围
		// 		hig: num  // 高于范围
		// 	],
		// 	[
		// 		low: num, // 低于范围
		// 		nor: num, // 正常范围
		// 		hig: num  // 高于范围
		// 	]
		// ]
		dataRange: {
			type: Array,
			default () {
				return []
			}
		},
		
		
		// 详情页对应序号 
		editId: {
			type: Number,
			dafault () {
				return -1
			}
		}
		
		
	},
	created() {
	},
	methods: {
		
		// 切换步数设备
		changeEq () {
			console.log('切换设备')
		},
		
		// 手动填写某项数据的值，跳转到编辑页面并且卸载其他所有页面；
		// 步数由微信运动或者蓝牙设备拿到，这里就不做处理了
		// 1 体重
		// 2 血压
		// 3 血糖
		// 4 心率
		// 5 体温
		// 6 总胆固醇
		// 7 尿酸
		onEditPage() {
			console.log(this.editId)
			uni.navigateTo({
				url: '/pages/add_data/addData?editId=' + this.editId
			})
			// 关闭所有页面，打开到应用内的某个页面。
			// 这里传参，然后编辑页面可以在onLoad拿到参数
		},
		
		noStepInfo () {
			console.log('弹窗')
		}
	},
	components: {
		vantLoading
	}
}
